:root {
  --accent1-color: rgb(0, 81, 61);
  --primary-interaction-color: rgb(36, 123, 160);
  --text-color: black;
  --background-color: white;
  --input-background-color: var(--background-color);
  --app-title-color: white;
  --selection-color: lightblue;
  --focus-border-color: var(--primary-interaction-color);
  --dealer-token-background: lightgray; }

@media (prefers-color-scheme: dark) {
  :root {
    --accent1-color: rgb(0, 9, 3);
    --app-title-color: rgb(0, 162, 121);
    --text-color: #eee;
    --background-color: #121212;
    --input-background-color: #242424;
    --selection-color: steelblue;
    --dealer-token-background: rgb(60, 60, 60);
    --focus-border-color: lightblue; } }

html,
body,
#root {
  height: 100vh;
  width: 100vw;
  overflow: hidden; }

body {
  position: fixed;
  background-color: var(--accent1-color);
  margin: 0;
  color: var(--text-color); }

#root {
  background-color: var(--background-color); }

* {
  box-sizing: border-box;
  font-family: 'San Francisco', 'Segoe UI', --apple-system, sans-serif; }
