.c-input {
  display: block; }

.c-base-input {
  --focus-shadow-color: transparent;
  background-color: var(--input-background-color);
  color: var(--text-color);
  border: 1px solid lightgray;
  border-radius: 5px;
  -webkit-appearance: none;
  box-shadow: 0 0 2px 3px var(--focus-shadow-color); }
  .c-base-input:focus {
    --focus-shadow-color: var(--focus-border-color);
    outline: none; }
  @media (prefers-color-scheme: dark) {
    .c-base-input {
      border-color: var(--dealer-token-background); } }
